<!-- 居民信息管理 -->
<template>
  <div class="list-common-box resident-information-box">
    <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb :isLevel="2"></Breadcrumb>
    </div>
    <!-- 主要数据 -->
    <div class="card-box">
      <!-- 搜索 -->
      <div class="search-bar">
        <!-- 左 -->
        <div class="search-left">
          <div class="search-item">
            <span class="search-label">身份证号</span>
            <el-input
              v-model.trim="searchKey.idNo"
              placeholder="请输入身份证号"
              clearable
            ></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">姓名</span>
            <el-input
              v-model.trim="searchKey.userName"
              placeholder="请输入姓名"
              clearable
            ></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">性别</span>
            <el-select placeholder="请选择性别" v-model="searchKey.sexType" clearable>
              <el-option
                v-for="item in sexOption"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
          <div class="search-item">
            <span class="search-label">签约状态</span>
            <el-select placeholder="请选择签约状态" v-model="searchKey.signStatus" clearable>
              <el-option
                v-for="item in signStatusOption"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="search-right">
          <el-button type="primary" plain icon="el-icon-search" @click="querylist()"
            >查询</el-button
          >
          <el-button type="primary" plain icon="el-icon-delete" @click="Refreshlist()"
            >清空</el-button
          >
        </div>
      </div>
      <!-- 表格 -->
      <div class="table-box">
        <el-table :data="dataList" tooltip-effect="dark" stripe height="100%">
          <el-table-column label="序号" align="center" width="80">
            <template slot-scope="scope">
              {{ scope.$index + (pageNum - 1) * pageSize + 1 }}
            </template>
          </el-table-column>
          <el-table-column prop="userName" label="姓名" align="center">
            <template slot-scope="scope">
              {{ scope.row.userName ? scope.row.userName : EMPTYTEXT }}
            </template>
          </el-table-column>
          <el-table-column prop="idNo" label="身份证号" align="center">
            <template slot-scope="scope">
              {{ scope.row.idNo ? scope.row.idNo : EMPTYTEXT }}
            </template>
          </el-table-column>
          <el-table-column prop="sexTypeStr" label="性别" align="center" width="70">
            <template slot-scope="scope">
              {{ scope.row.sexTypeStr ? scope.row.sexTypeStr : EMPTYTEXT }}
            </template>
          </el-table-column>
          <el-table-column prop="age" label="年龄" align="center" width="70">
            <template slot-scope="scope">
              {{ scope.row.age !== null ? scope.row.age + '岁' : EMPTYTEXT }}
            </template>
          </el-table-column>
          <el-table-column prop="signStatusStr" label="签约状态" align="center">
            <template slot-scope="scope">
              {{ scope.row.signStatusStr ? scope.row.signStatusStr : EMPTYTEXT }}
            </template>
          </el-table-column>
          <el-table-column prop="registerOrganization" label="建档机构" align="center">
            <template slot-scope="scope">
              {{ scope.row.registerOrganization ? scope.row.registerOrganization : EMPTYTEXT }}
            </template>
          </el-table-column>
          <el-table-column prop="createDateStr" label="建档时间" align="center" width="170">
            <template slot-scope="scope">
              {{ scope.row.createDateStr ? scope.row.createDateStr : EMPTYTEXT }}
            </template>
          </el-table-column>
          <el-table-column prop="registerUser" label="建档医生" align="center" width="100">
            <template slot-scope="scope">
              {{ scope.row.registerUser ? scope.row.registerUser : EMPTYTEXT }}
            </template>
          </el-table-column>
          <el-table-column label="操作" align="center" width="200">
            <template slot-scope="scope">
              <el-button type="primary" @click="handleView(scope.row)" class="green"
                >查看详情</el-button
              >
              <el-button type="primary" @click="handleRecord(scope.row)">服务记录</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 分页 -->
      <div class="page-box">
        <Paginaction
          class="pagination"
          :goPage="pageNum"
          :totalNum="totalNum"
          @updateList="changePage"
          ref="paginationEvent"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'resident-information',
  data() {
    return {
      searchKey: {
        idNo: '', //身份证号
        userName: '', //姓名
        sexType: '', //性别
        signStatus: '' //签约状态
      },
      sexOption: [
        {
          value: 0,
          label: '全部'
        },
        {
          value: 1,
          label: '男'
        },
        {
          value: 2,
          label: '女'
        }
      ],
      signStatusOption: [
        {
          value: 0,
          label: '全部'
        },
        {
          value: 1,
          label: '已签约'
        },
        {
          value: 2,
          label: '未签约'
        }
      ], //签约状态下拉数据
      pageNum: 1, //当前页
      pageSize: 10, //每页条数
      totalNum: 0, //总条数
      dataList: [] //居民信息列表
    }
  },
  created() {
    this.getDatalist() //获取居民信息列表
  },
  methods: {
    //查看详情
    handleView(scope) {
      this.$router.push({
        path: '/resident-manage/resident-information/detail?id=' + scope.recordId
      })
    },
    //签约记录
    handleRecord(scope) {
      this.$router.push({
        path: '/resident-manage/resident-information/service-record?id=' + scope.recordId
      })
    },
    //查询
    querylist() {
      this.pageNum = 1
      this.getDatalist()
    },
    //重置
    Refreshlist() {
      this.pageNum = 1
      this.pageSize = 10
      Object.keys(this.searchKey).forEach(k => (this.searchKey[k] = ''))
      this.getDatalist()
    },
    //分页改变
    changePage(query) {
      this.pageNum = query.pageNum
      this.pageSize = query.pageSize
      this.getDatalist()
    },
    //获取居民信息列表
    getDatalist() {
      this.$emit('viewLoad', true)
      let params = {}
      params.pageNum = this.pageNum
      params.pageSize = this.pageSize
      params = Object.assign({}, params, this.searchKey)
      this.$http
        .post(process.env.VUE_APP_API + '/family/person/getDtoByPage', params)
        .then(res => {
          if (res.code === 200) {
            this.dataList = res.data.list || [] //居民信息
            this.totalNum = res.data.total //总条数
            this.pageNum = res.data.pageNum //当前页
            this.pageSize = res.data.pageSize //每页条数
          } else {
            this.$message.error(res.msg)
          }
          this.$emit('viewLoad', false)
        })
        .catch(error => {
          this.$emit('viewLoad', false)
          this.$message.error(error)
        })
    }
  }
}
</script>
<style lang='less' scoped>
</style>